@import "../../variables.modules";

// Improve responsiveness using clamp()
h1 {
  font-size: clamp(1.75rem, 4vw, 3rem);
  text-align: center;
  
  @media only screen and (min-width: 940px) {
    text-align: left;
  }
}

h2 {
  font-size: clamp(1.25rem, 3vw, 2rem);
  text-align: center;
  
  @media only screen and (min-width: 940px) {
    text-align: left;
  }
}

// Hand animation improved for smooth motion
.hand {
  animation-name: wave;
  animation-duration: 2s;
  animation-iteration-count: infinite;
  transform-origin: 70% 70%;
  display: inline-block;
}

// Improved link hover effect with smooth transition
a {
  color: $dark;
  transition: color 250ms ease-in-out, transform 100ms ease;
  text-decoration: none; // Remove underline by default

  &:hover {
    color: $lightgray;
    transform: scale(1.05); // Slight zoom effect on hover
  }
}

// Keyframes with smoother animation transitions
@keyframes wave {
  0%, 100% { transform: rotate(0deg); }
  10% { transform: rotate(12deg); }
  20% { transform: rotate(-8deg); }
  30% { transform: rotate(12deg); }
  40% { transform: rotate(-6deg); }
  50% { transform: rotate(8deg); }
  60% { transform: rotate(0deg); }
}
