@import "../../variables.modules";

// Improved icon styling with spacing & hover effects
i {
  padding-left: 0.5rem;
  transition: color 0.3s ease-in-out; // Smooth transition on hover

  &.red {
    color: $red;
  }

  &.amber {
    color: $yellow;
  }

  &.green {
    color: $green;
  }

  &:hover {
    filter: brightness(1.2); // Slightly brightens on hover
  }
}

// Terminal styling with improved readability
.terminal {
  p, li {
    margin: 1rem 0;
    color: $white;
    line-height: 1.6; // Improves readability
  }

  a, a:visited {
    animation: changeColors 4s infinite alternate ease-in-out;
    font-weight: bold;
    text-decoration: none;

    &:hover {
      text-decoration: underline; // Helps users recognize clickable links
    }
  }
}

// Smoother color transition animation
@keyframes changeColors {
  0% {
    color: #00a47f;
  }
  33% {
    color: #1d91e3;
  }
  66% {
    color: #d419fe;
  }
  100% {
    color: #ff9800; // Added a warm color for better contrast
  }
}
